import React, { Component } from 'react';
import { connect } from 'react-redux';
import NoRecord from '../customFields/noRecord'
import Loader from '../customFields/loader';

import { getIndividualItem, resetPageStore } from '../../actions/page';
import { Endpoint } from '../../utils/constants';
import { exportToCsv, print } from '../../utils/formatter';

class AttendeesList extends Component {
    constructor(props) {
        super(props);
        this.keymapper = {
            header: ["MEMBERS", "GUESTS"],
            keys: ["name", ["gName", "withGuest"]]
        }
        this.state = {
            list: []
        };
    }

    componentDidMount(){
       if (this.props.id) {
        this.props.getIndividualItem({
            params: { token: this.props.token, type: 'attendees' },
            url: `${Endpoint.EVENT_MANAGE}/${this.props.id}`
        });
        }
    }

    componentWillUnmount() {
        this.props.resetPageStore('individualItem');
    }

    render() {
        let { itemLoader, list, data} = this.props;
        return (
            <div className='eventModal attendees-list' id="_prinnt_doc">
                <div className='row'>
                    <div className='col-5'>
                        <p className='title-txt'>{data.title}</p>
                    </div>
                    <div className='col-3'>
                        <p className='title-txt'>{data.eventDate}</p>
                    </div>
                    <div className='col-4 text-right'>
                        <p className='title-txt text-uppercase'>{data.venue}</p>
                    </div>
                </div>
                <p className='color-white'>List of Attendees</p>
                <div className='row mt10 mb15'>
                    <div className='col-4'><p className='color-gray'>MEMBERS</p></div>
                    <div className='col-6'><p className='color-gray'>GUESTS</p></div>
                </div>
                {itemLoader && <Loader />}
                {!itemLoader && list.length === 0 && <NoRecord />}
                {!itemLoader && list.length > 0 && list.map((item, i)=>{
                    return(
                        <div className='row mb5' key={i}>
                            <div className='col-4'><p className='color-white'>{item.name}</p></div>
                            <div className='col-3'><p className='color-white'>{item.gName || '--'}</p></div>
                            <div className='col-1'><p className='color-white'>{item.withGuest || '--'}</p></div>
                            {/* <div className='col-4 text-center'><p className='color-white'>--</p></div> */}
                        </div>
                    )
                })
                }
               {!itemLoader && list.length > 0 && 
                <div className='text-right mt20' >
                    <div className='btn mr10' onClick={()=>exportToCsv(list, data.title,  this.keymapper)}>Download</div>
                    {/* <div className='btn' >Print</div> */}
                </div>
               }
            </div>
        );
    }
}
function mapStateToProps({ auth: { token }, page: {individualItem: { data = {} }, itemLoader = false } }) {
    return { token, list: data, itemLoader }
}

export default connect(mapStateToProps,{getIndividualItem, resetPageStore})(AttendeesList);
